import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';

const DirectoryView = (props) => (
    <div>
        <SearchForm searchVal={props.searchVal} handleChange={props.handleChange} />
        {props.recipes.map((recipe, i) => <RecipeCard name={recipe.name}
        key={i} />)}

       
    </div>
);

const SearchForm = (props) => (
    <form>
        <input value={props.searchVal} onChange={props.handleChange} />
        <button>Search</button>
    </form>
);

const RecipeCard = (props) => (
    <div>
        <p>{props.name}</p>
    </div>
);

const DetailView = props => (
    <article>
      <h1>{props.detailRecipe.name.toUpperCase()}</h1>
      <div className={'details'}>
        <div id={'ingredients'}>
          <h5>INGREDIENTS</h5>
          <ul>
            {props.detailRecipe.ingredients.map(ingredient => (
              <li>
                <p>{ingredient}</p>
              </li>
            ))}
          </ul>
        </div>
        <div id={'instructions'}>
          <h5>INSTRUCTIONS</h5>
          <ul>
            {props.detailRecipe.instructions.map(instruction => (
                <li>
                  <p>{instruction}</p>
                </li>
            ))}
          </ul>
        </div>
      </div>
    </article>
  );

class App extends React.Component{
    state = {
        recipes: [ 
       { 
        id: 1,
        name: 'shortbread cookie',
        ingredients: ['dough', 'sugar', 'whipped cream'],
        instructions: ['pre-heat over to 350', 'tickle the cookie', 'laugh']
        },
        {
        id: 2,
        name: 'cheesecake',
        ingredients: ['cream cheese', 'cakemix', 'sugar'],
        instructions: ['pre-heat over to 350', 'tickle the cheese', 'cry']
        },
        {
        id: 3,
        name: 'chocolate chip cookies',
        ingredients: ['chocolate', 'chip', 'dough'],
        instructions: ['pre-heat over to 350', 'tickle the chocolate', 'mix']
        },
        {
        id: 4,
        name: 'cheesecake',
        ingredients: ['cream cheese', 'cakemix', 'sugar'],
        instructions: ['pre-heat over to 350', 'tickle the cheese', 'cry']
        },
        {
        id: 5,
        name: 'cheesecake',
        ingredients: ['cream cheese', 'cakemix', 'sugar'],
        instructions: ['pre-heat over to 350', 'tickle the cheese', 'cry']
        },
        {
        id: 6,
        name: 'cheesecake',
        ingredients: ['cream cheese', 'cakemix', 'sugar'],
        instructions: ['pre-heat over to 350', 'tickle the cheese', 'cry']
        },
        {
        id: 7,
        name: 'cheesecake',
        ingredients: ['cream cheese', 'cakemix', 'sugar'],
        instructions: ['pre-heat over to 350', 'tickle the cheese', 'cry']
        },
        {
        id: 8,
        name: 'cheesecake',
        ingredients: ['cream cheese', 'cakemix', 'sugar'],
        instructions: ['pre-heat over to 350', 'tickle the cheese', 'cry']
        },
        {
        id: 9,
        name: 'cheesecake',
        ingredients: ['cream cheese', 'cakemix', 'sugar'],
        instructions: ['pre-heat over to 350', 'tickle the cheese', 'cry']
        }
        
    ],
    
    searchVal: '',
    selectedRecipes: [],
    detailRecipe: {
      id: -1,
      name: "",
      ingredients: [],
      instructions: []
    }
  };

  handleChange = event => {
    event.preventDefault();
    this.setState({ searchVal: event.target.value});
  }

  selectRecipes = event => {
    event.preventDefault();
    const selects = this.state.recipes.filter(recipe => recipe.name.includes(this.state.searchVal));
    this.setState({selectedRecipes: selects});
  }

  selectDetail = (event, id) => {
    event.preventDefault();
    const recipe = this.state.recipes.find(recipe => recipe.id === id)
    this.setState({detailRecipe: recipe})
  }

  //Executed after render() finishes
  componentDidMount() {
    //Initializes page will all recipes
    this.setState({ selectedRecipes: this.state.recipes });
    this.setState({detailRecipe: this.state.recipes[0]})
  }

  render() {
    return (
      <DirectoryView
        selectedRecipes={this.state.selectedRecipes}
        searchVal={this.state.searchVal}
        detailRecipe={this.state.detailRecipe}
        handleChange={this.handleChange}
        selectRecipes={this.selectRecipes}
        selectDetail={this.selectDetail}
      />
    );
  }
}

ReactDOM.render(<App />, document.getElementById("root"));